import { Account } from "../../../hooks/use_account";
import { SmartState } from "../../../hooks/use_smart_state";
import { securePage } from "../../../components/common/secure_page";
import Form from "../../../components/common/form/form";
import Title from "../../../components/common/form/title";
import { formParentManageAccount } from "../manage-account";
import Box from "../../../components/common/form/box";
import P from "../../../components/common/form/p";
import Input from "../../../components/common/form/input";
import SubmitButton from "../../../components/common/form/submit-button";
import { formRequestAccountManagerAccountEmail } from "../../../lib/shared/form-request-makeup/requests/account/manage-account/email";
import Desktop from "../../../components/common/desktop";
import Mobile from "../../../components/common/mobile";

export type AccountManageAccountEmailPageProps = {
    result: {};
    account: SmartState<Account | null>;
};

const AccountManageAccountEmailPage = securePage(function({result,account}: AccountManageAccountEmailPageProps) {
    const { user: { email } } = account.value;

    return (<>
        <div className="account-manage-account-email-page">            
            <Form 
                requestMakeup={formRequestAccountManagerAccountEmail}
                parent={formParentManageAccount}
                autoFocus
                onSuccess={({otpId}: {otpId: string}) => {
                    return {
                        type: "redirect",
                        path: `/account/manage-account/email/confirm?id=${encodeURIComponent(otpId)}`
                    };
                }}
            >
                <Title>Change your email</Title>
                <Box border>
                    <P>
                        We will send an OTP to the new email address which you will have to enter on the next screen to verify that you own it.
                    </P>
                    <Input name="email" initialValue={email || ""} />
                    <SubmitButton caption="Request OTP" disableIfUnchanged={["email"]} />
                </Box>
            </Form>
        </div>
        <style jsx>{`
            .account-manage-account-email-page {
                /* min-height: 600rem; */
                width: 100%;
            }
        `}</style>
    </>
);
});

export default AccountManageAccountEmailPage;